.nav{
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  background: white;
  position: relative;
  margin-bottom: 80px;

  @media ($desktop) {

    &:before, &:after{
      content: " ";
      height: 5px;
      width: 75px;
      display: block;
      background: #000;
      bottom: 13px;
      position: absolute;
      transition: 150ms ease-in;
    }

    &:before{
      left: -75px;
    }
    &:after{
      right: -75px;
    }
  }

  @media (min-width: 940px){
    &:before, &:after{
      width: 135px;
    }
    &:before{
      left: -135px;
    }
    &:after{
      right: -135px;
    }
  }
}

.nav-link{
  text-decoration: none;
  color: $color-body;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 300;
  margin: 0 20px;
  transition: 150ms ease-in;
  display: block;
  padding: 10px 0;

  @media ($desktop){
    display: inline-block;
    padding: 0;
  }

  &--active, &:hover{
    color: $color-main;
  }

  &:first-of-type{
    @media ($desktop){
      margin-left: 0;  
    }
    
  }
  &:last-of-type{
    @media ($desktop){
      margin-right: 0;
    }
  }
}
