@charset "UTF-8";

// Imports and variables
@import 'variables';
@import 'helpers';
@import 'nav';

*, *:before, *:after {
  box-sizing: inherit;
}

html{
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  color: $color-body;
  font-family: 'Oswald', sans-serif; // 300, 400, 700
  font-weight: 300;
}

body{
  margin: 0;
}

::selection {
  background-color: $color-main;
  color: white;
}

img{ max-width: 100%; }


.owa-logo{
  font-size: 36px;
  text-transform: uppercase;
  font-weight: 500;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 40px;
}

.owa-title{
    @extend %hide-text;
    height: 0;
    margin: 0;
    padding: 0;
}

.owa-img{
  width: 125px;
  transition: 200ms;

  &:hover{
    fill: $color-main;
    transition: 200ms;
  }
}

.container{
  padding: 0 15px;
  max-width: 925px;
  margin: 0 auto;
  @media (min-width: 926px) {
    padding: 0;
  }
}

.section-intro{
  margin-bottom: 80px;
}

.section-title{
  font-size: 40px;
  text-transform: uppercase;
  margin: 0 0 40px;
}

.work-title{
  @extend .section-title;
  text-align: center;
}

.work{
  &__img{
    max-width: 100%;
    margin-bottom: 16px;
    margin-left: auto;
    margin-right: auto;
  }

  &__description{
    margin-bottom: 60px;
  }
}


.intro{
  max-width: 690px;
  margin: 0 auto;
  padding: 0 15px;
  text-align: center;
  font-weight: 300;
  font-size: 28px;
}

.section-feature{
  @extend %clearfix;
  background-color: $color-main;
  color: white;
  padding: 40px 0;
  overflow-x: hidden;

  .container{
    padding: 0 20px 0 15px;
  }
}

.feature-list{
  @extend %clearfix;
  @media ($desktop) {
    margin: 0 -20px;
  }
}

.feat-col{
  @media ($desktop) {
    width: 33.33333%;
    float: left;
    padding: 0 20px;
    min-height: 243px;
  }
}


.feature{

  position: relative;
  display: block;
  transition: 150ms ease-in;
  margin-bottom: 40px;
  text-decoration: none;
  box-shadow: 5px 5px 0px #fff3f6;


  &__img{
    display: block;
    margin: 0 auto;
  }

  &__overlay{
    opacity: 0;
    pointer-events: none;
    padding: 20px;
    background-color: $color-main;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    transition: 150ms ease-in;
  }

  &__overlay-data{
    position: absolute;
    opacity: 0;
    left: 25px;
    top: 20px;
    transition: 150ms ease-in;
    color: white;
  }

  &:hover, &.hover{
    transition: 150ms ease-in;

    .feature__overlay{
      opacity: .95;
      pointer-events: all;
      transition: 150ms ease-in;
      border: 7px solid white;
    }

    .feature__overlay-data{
      opacity: 1;
      transition: 0 ease-in;
    }
  }

  &__title{
    text-transform: uppercase;
    font-size: 36px;
    font-weight: bold;
    margin: 0;
  }

  &__view{
    text-transform: uppercase;
    font-weight: 300;
    margin: 0;
    font-size: 18px;
  }

}

.section-about{
  @extend %clearfix;
  padding: 40px 0;
}

.row{
  @extend %clearfix;
}

.person{

  margin-bottom: 80px;

  @media(min-width: 1040px){
    float: left;
    width: 50%;
  }

  &__band{
    background-color: $color-main;
    color: white;
    text-transform: uppercase;
    position: relative;
    width: 100%;
    padding: 10px 15px 10px 0;
    margin-bottom: 25px;
    height: 83px;

    .person--right &{
      padding-left: 15px;
    }

  }

  &__data{

    position: relative;
    text-align: left;
    padding-left: 130px;

    @media(min-width: 1040px){
      width: 480px;
      float: right;
    }

    .person--right &{
      text-align: right;
      padding-right: 140px;
      padding-left: 0;

      @media(min-width: 1040px){
        float: left;
        text-align: left;
        padding-left: 0;
        padding-left: 130px;
      }
    }
  }

  &__name{
    margin: 0;
    font-size: 24px;
    font-weight: normal;
  }

  &__position{
    margin: 0;
    font-size: 16px;
    font-weight: normal;
  }

  &__img{
    position: absolute;
    border-radius: 50%;
    height: 100px;
    width: 100px;
    bottom: -22px;
    left: 15px;
    filter: grayscale(1);
    user-select: none;

    @media (min-width: 1040px){
      left: 10px;
    }
    .person--right &{
      right: 15px;
      left: initial;
      @media (min-width: 1040px){
        left: 10px;
        right: auto;
      }
    }

  }

  &__bio{
    padding: 0 15px;
    font-size: 17px;
    clear: both;
    line-height: 1.2;

    @media (min-width: 1040px){
      max-width: 480px;
      float: right;
      padding: 0 30px 0 0;
    }

    .person--right &{
      @media (min-width: 1040px){
        float: left;
        padding: 0 0 0 30px;
      }
    }

  }

}

.section-contact{
  @extend %clearfix;
  background: $color-main;
  color: white;
  padding: 40px 0;

  .mail{
    font-size: 38px;
  }
}

figure{
  img{
    display: block;
  }
}

figcaption{
  font-size: 12px;
  font-family: -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  color: #666;
  a{
    color: lighten($color-main, 20%);
  }
}

.bottom__img{
  fill: white;
  width: 100px;
}
